.users {
  display: flex;
  flex-direction: column;
  place-items: center;
}
.users__heading {
  text-align: center;
  margin-bottom: 50px;
}
.users__list {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 50px;
}

.card {
  padding: 20px;
  background: #FFFFFF;
  border-radius: 10px;
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 16px;
  }
  @media (min-width: $desktop-width) {
    margin-bottom: 29px;
  }
}
.card__list {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.card__photo {
  margin-bottom: 20px;
}
.card__name {
  margin-bottom: 20px;
}
.card__email a,
.card__number a {
  color: $text-color;
  text-decoration: none;
}
